html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-body);
  color: var(--black);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

.wrapper {
  width: min(100% - 2.5rem, 75rem);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-header);
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 0.98rem + 3.35vw, 3.5rem);
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

a[aria-current=true] {
  font-weight: 700;
  text-decoration: underline;
}

:root {
  --darkGreen: hsl(167, 97%, 15%);
  --nwcGreen: hsl(166, 100%, 20%);
  --lightGreen: hsl(109, 61%, 43%);
  --northernBlue: hsl(208, 100%, 33.5%);
  --lightBlue: hsl(214, 58%, 55%);
  --orange: hsl(31, 100%, 50%);
  --red: hsl(347, 100%, 45.3%);
  --yellow: hsl(49, 100%, 50%);
  --white: hsl(240, 100%, 99%);
  --black: hsl(40, 8%, 15%);
}

:root {
  --font-header: "Roboto", sans-serif;
  --font-body: "Libre Baskerville", serif;
}

.card {
  padding: 1.25rem;
}

.header {
  background-color: var(--nwcGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 998;
}
.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(var(--nwcLightGreen), transparent);
}
.header__logo {
  padding: 1rem 0;
}
.header__logo img {
  width: 8.75rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.header__nav--open {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--nwcGreen);
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
  top: 125px;
  padding: 20px;
  z-index: 990;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
}
.header__media {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__media {
    flex-direction: column;
  }
}
.header__search {
  margin-left: 1.875rem;
}
@media (max-width: 59.999375em) {
  .header__search {
    width: 100%;
    margin: 1.25rem 0 0;
  }
}
.header__search input[type=text] {
  font-size: 1rem;
  color: var(--lightGrey);
}
.header__search--placeholder {
  padding: 0.3125rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--newGreen);
}
@media (max-width: 59.999375em) {
  .header__search--placeholder {
    width: 100%;
  }
}
@media (max-width: 59.999375em) {
  .header__social {
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__social a {
  margin-left: 1.25rem;
}
.header__social a:first-child {
  margin-left: 0;
}
.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  padding-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__menu {
    flex-direction: column;
  }
}
.header__menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--font-header);
  font-size: 1.125rem;
  font-weight: 700;
  letter-spacing: 0.75px;
  list-style: none;
}
@media (max-width: 59.999375em) {
  .header__menu li {
    margin: 0.625rem 0;
  }
}

@media (max-width: 59.999375em) {
  .head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: var(--nwcGreen);
  width: 100%;
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 1.25rem;
  }
}
.nav__list {
  display: flex;
  padding: 0;
  margin: 0;
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
  width: 100%;
}
@media (min-width: 43.75em) {
  .nav__item:hover ul.submenu, .nav__item:focus ul.submenu {
    width: 100%;
    opacity: 1;
    visibility: visible;
    display: flex;
    flex-direction: column;
    width: 18rem;
  }
}
.nav__list, .nav__item {
  margin-left: 1.875rem;
}
.nav__list:first-child, .nav__item:first-child {
  margin-left: 0;
}
@media (max-width: 59.999375em) {
  .nav__list, .nav__item {
    display: none;
  }
}
.nav__list a, .nav__item a {
  color: var(--white);
  padding: 1rem 0;
  cursor: pointer;
}
.nav__list a:hover, .nav__item a:hover {
  text-decoration: none;
  color: var(--darkGreen);
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
}
.has-submenu ul.submenu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .has-submenu ul.submenu {
    opacity: 1;
    visibility: visible;
  }
}

.submenu {
  display: none;
  justify-content: center;
  padding-left: 0;
  width: 100%;
}
@media (min-width: 60em) {
  .submenu {
    display: none;
    justify-content: center;
    padding-left: 0;
    align-items: center;
    text-align: center;
    position: absolute;
    background-color: var(--nwcGreen);
    padding: 0;
    top: 2em;
    margin-top: 1em;
  }
}
.submenu li {
  margin-left: 0;
}

@media (max-width: 59.999375em) {
  .submenu-active .submenu {
    display: block;
    background-color: var(--nwcLightGreen);
  }
}

.nav__item {
  text-align: center;
  display: none;
}

.active .nav__item {
  display: block;
}

.subitem {
  color: var(--white);
  width: 100%;
  margin: 0;
  padding: 0.75rem 0;
}
.subitem:hover, .subitem:focus {
  background-color: var(--yellow);
}
.subitem:last-child {
  padding-bottom: 0.75em;
}
.subitem a {
  padding: 0;
  gap: 0;
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

.drop {
  display: flex;
  flex-direction: column;
  z-index: 999;
}

.nav__item.has-submenu.submenu-active {
  background-color: transparent;
}